<template>
  <transition name="slide">
    <music-list :songs="songs" :title="title" :father-image="bgImage"></music-list>
  </transition>
</template>

<script>
  import MusicList from '../music-list/music-list.vue'
  import {getDiscDetail} from '../../api/recommend'
  import {ERR_OK} from "../../api/config";
  import {mapGetters} from 'vuex'
  import {createSong} from '../../common/js/song'

  export default {
    created() {
      this.receiveDisc()
    },
    data() {
      return {
        songs: []
      }
    },
    computed: {
      //唱片标题
      title() {
        return this.disc.dissname
      },
      //唱片代表图（背景墙图片
      bgImage() {
        return this.disc.imgurl
      },
      ...mapGetters([
        'disc'
      ])
    },
    methods: {
      //接收disc数据
      receiveDisc() {
        if (!this.disc.dissid) {//没有disc id 返回父路由（父页面
          this.$router.push({//相当于<router-link :to="recommend">
            path: '/recommend-page'
          });
          return;
        }
        getDiscDetail(this.disc.dissid)
          .then((res) => {
            if (res.code === ERR_OK)
              this.songs = this._normalizeDisc(res.cdlist[0].songlist);
          })
      },
      //辅助处理数据
      //歌曲数据列表转换成歌曲类
      _normalizeDisc(list) {
        let result = [];
        list.forEach((item) => {
          if (item.songid && item.albummid) {
            result.push(createSong(item))//如果直接用构造函数new，需要传入一串函数，
            // 所以用另一个函数createSong，传入一个参数，进而用createSong调用构造函数new
          }
          else {
            console.log("disc返回的歌曲数据列表有一条无法创建歌曲类")
          }
        });
        return result;
      }
    },


    components: {
      MusicList
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .slide-enter-active, .slide-leave-active
    transition: all 0.3s

  .slide-enter, .slide-leave-to
    transform: translate3d(100%, 0, 0)
</style>



